.home-page {
    position: relative;
}

.background-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    min-height: 700px;
    object-fit: cover;
    z-index: -5;
}

.welcome-screen-wrapper {
    min-height: 100vh;
}

.page-center{
    margin: 0 auto 0 auto;
    display: flex;
    min-height: 500px;
    flex-direction: column;
    align-items: center;
}

.name-middle {
    font-family: "Anton", sans-serif;
    text-shadow: #962d2f 0 0.8vw;
}

.name-middle-trix {
    font-size: clamp(100px, 10vw, 10em);
}

.hp-buttons {
    margin-top: 2.5vh;
    display: flex;
    flex-direction: row;
    width: 40%;
}

.hp-button {
    display: inline-block;
    font-family: Montserrat, sans-serif;
    font-size: clamp(25px, 1.2vw, 3em);
    font-weight: bolder;
    padding: 15px;
    width: 75%;
    border: 2px solid black;
    text-align: center;
    background-image: linear-gradient(180deg, #f37278, #641719);
    color: #fbf187;
    text-shadow: #641719 0 0.4vh;
    border-radius: 25px;
}

.hp-button:hover {
    background-image: linear-gradient(180deg,  #fbf187, #c5c514);
    color: #9b3d3f;
    text-shadow: #bbbb2c 0 0.25vh;
}

.hp-button:first-child {
    margin-right: 50px;
}

.disabled {
    pointer-events: none;
    cursor: default;
}

.trix-logo {
    width: 25vw;
    max-width: 750px;
    min-width: 250px;
}

.video-reveal-container {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5vh 0;
    width: 100%;
    background-image: url("../../assets/images/video-background.webp");
    background-repeat: round;
}

.video-reveal {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 70%;
    height:  auto;
}

iframe {
    width: 700%;
    height: 50%;
    min-height: 700px;
}

.time-until-conference-container {
    margin: 0 auto 0 auto;
    padding-top: 25vh;
    padding-bottom: 25vh;
    width: 100%;
    background-image: url("../../assets/gif/dragon_loading.gif");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    display: flex;
    flex-direction: column;
    text-align: center;
    color: #fcdc58;
    text-shadow: black 0.3vw 0.4vw;
}

.countdown {
    font-size: clamp(40px, 5vw, 10em);
    font-weight: bolder;
}

.time-until-conference {
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding-top: 15vh;
}

.time-until-conference > div {
    display: flex;
    flex-direction: column;
    font-size: clamp(20px, 3vw, 10em);
    font-family: "Anton", sans-serif;
    padding-right: 12vw;
}

.time-until-conference > div > div {
    padding-bottom: 2vh;
}

.time-until-conference > div:last-child {
    padding-right: 0;
}

@media screen and (max-width: 1000px) {
    .page-center {
        margin-top: 15vw;
    }

    .hp-buttons {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .hp-buttons > a {
        width: 100%;
    }

    .hp-buttons > a:first-child {
        margin-right: 0;
        margin-bottom: 15px;
    }
}

@media screen and (max-width: 750px) {
    .time-until-conference-container {
        padding-top: 15vh;
        padding-bottom: 15vh;
    }

    .time-until-conference > div {
        padding-right: 7vw;
    }

    iframe {
        min-height: 315px;
    }
}